.header {
	position: relative;
	background: url(../images/header.jpg)no-repeat center/cover;
	height: 100vh;
}

.top-header {
	position: relative;
	padding: 20px 0;
	.container {
		position: relative;
	}
	&__item {
		display: inline-block;
		color: #fff;
	}
	&__link {
		font-weight: 400;
		font-size: 20px;
		transition: color .3s ease;
		&:hover {
			color: @lime;
		}
	}
	&__btn {
		margin-left: 15px;
		text-transform: uppercase;
	}

	.form-box {
		position: absolute;
		top: 0;
		left: 250px;
		padding: 22px;
		padding-top: 0;
		display: flex;
		flex-direction: column;
		align-self: center;
		text-align: center;
		width: 270px;
		font-family: @ff2;
		background: rgba(0,0,0, .6);
		z-index: 10;

		&__title {
			font-size: 26px;
			font-family: @ff2;
			font-weight: 700;
		}
		&__text {
			font-size: 20px;
			font-weight: 400;
		}
		.form__line {
			width: 90%;
			padding: 5px 15px;
			margin: 10px auto;
			background: #fff;
			border: none!important;
			font-size: 22px;
			font-weight: 300;
			outline: none;
		}
		.btn__callback {
			width: 90%;
			padding: 10px;
			margin: 10px auto;
			background: @lime;
			border: none;
			font-size: 20px;
			font-weight: 700;
			text-transform: uppercase;
			transition: background-color .3s ease;
			&:hover{
				background: #fff;
			}
		}
	}
}



.center-header {
	padding-top: 80px;
	.container-fluid {
		position: relative;
		max-width: 1400px;
		&:before {
			content: '';
			position: absolute;
			top: 0;
			width: 100%;
			height: 610px;
			background: url(../images/border.png) no-repeat;
		}
	}
	&__title {
		padding-top: 60px;
		font-family: @ff2;
		font-size: 112px;
		line-height: 1;
		letter-spacing: .3px;
		text-transform: uppercase;
		color: @lime;
	}
	&__slogan {
		margin-top: -60px;
		font-size: 36px;
		font-family: @ff2;
		font-weight: 700;
		line-height: 1;
	}
	&__img {
		display: block;
		animation: astro-bounce 5s infinite ease-in-out;   
		&:hover {
			animation-play-state: paused;
		}
	}
}



@keyframes astro-bounce {
	0%, 20%, 50%, 80%, 100% {
		transform: translateY(0);
	}
	50% {
		transform: translateY(-5px);
	}
}